<template>
  <div class="container mb-14 p-8">
    <img
      alt="bees"
      src="../assets/bees_thumb.png"
      style="
        opacity: 60%;
        width: 35%;
        display: block;
        margin-left: auto;
        margin-right: auto;
      "
    />
    <p class="mt-8 font-medium font-sans text-2xl">Poll'em</p>
    <p class="font-sans mb-8">
      A simple, hassle-free and performant polling application written in
      Haskell.
    </p>
    <div>
      <tabs v-model="active">
        <tab title="Your account">
          <div class="m-4" v-if="!user.hash">
            <p>
              You do not appear to be authenticated on this app. This means you
              can only view already existing polls. How about you authenticate
              so that you can start creating polls?
            </p>
            <div class="m-8">
              <div>
                <input
                  v-model="user.email"
                  type="text"
                  placeholder="email address"
                />
                <button
                  v-if="!user.token_asked"
                  v-on:click="askToken"
                  class="p-1 bg-yellow-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-700 focus:ring-opacity-75"
                >
                  Authenticate
                </button>
                <button
                  v-else
                  class="disabled:opacity-50 p-1 bg-green-500 text-white font-semibold rounded-lg shadow-md"
                  disabled
                >
                  Authenticate
                </button>
              </div>
              <div v-if="user.token_asked" class="mt-4">
                <input
                  v-model="user.token"
                  type="text"
                  placeholder="paste the token here"
                />
                <button
                  v-if="user.token.length > 3 && !user.token_sent"
                  v-on:click="confirmToken"
                  class="p-1 bg-yellow-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-700 focus:ring-opacity-75"
                >
                  Confirm token
                </button>
                <button
                  v-if="user.token_sent"
                  class="disabled:opacity-50 p-1 bg-green-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-700 focus:ring-opacity-75"
                  disabled
                >
                  Confirm token
                </button>
              </div>
            </div>
          </div>
          <div v-else>
            <p>Welcome, {{ user.email }}</p>
            <button
              v-on:click="logout"
              class="p-1 bg-red-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-700 focus:ring-opacity-75"
            >
              Logout
            </button>
          </div>
        </tab>
        <tab title="Poll on display">
          <button v-on:click="testChart">Test chart</button>
          <div v-if="takingPoll.startDate">
            <div id="takingPoll" class="flex flex-col space-y-3">
              <vue-echarts
                :option="chart.options"
                style="margin: 0 auto; height: 300px; width: 900px"
                ref="chart"
                v-if="chart.results.length > 0"
              />
              <div>
                <label class="mr-4" for="question">Question</label>
                <textarea
                  id="takingQuestion"
                  rows="2"
                  cols="30"
                  v-bind:value="takingPoll.question"
                  disabled
                />
              </div>
              <div>
                <label class="mr-4" for="takingDescription">Description</label>
                <textarea
                  id="takingDescription"
                  rows="3"
                  cols="45"
                  v-bind:value="takingPoll.description"
                  disabled
                />
              </div>
              <div>
                <label for="takingMultiple"
                  >Allow to select multiple answers</label
                >
                <input
                  class="mx-3 mr-5"
                  type="checkbox"
                  id="takingMultiple"
                  :checked="takingPoll.multiple"
                  disabled
                />
                <label for="takingVisible"
                  >Make results visible during poll</label
                >
                <input
                  class="mx-3 mr-5"
                  type="checkbox"
                  id="takingVisible"
                  :checked="takingPoll.visible"
                  disabled
                />
              </div>
              <div class="space-x-3">
                <label for="takingStartDate">Poll started at:</label>
                <input
                  type="text"
                  id="takingStartDate"
                  v-bind:value="takingPoll.startDate"
                  disabled
                />
                <div v-if="takingPoll.endDate">
                  <label class="space-y-3" for="takingEndDate"
                    >(optional) Poll due to close at:</label
                  >
                  <input
                    type="text"
                    id="takingEndDate"
                    v-bind:value="takingPoll.endDate"
                    disabled
                  />
                </div>
              </div>
              <button
                v-on:click="takePoll"
                class="p-1 bg-yellow-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-700 focus:ring-opacity-75"
              >
                Take the poll
              </button>
            </div>
          </div>
          <div v-else><p>Nothing to render.</p></div>
        </tab>
        <tab title="Create a poll">
          <div id="creatingPoll" class="flex flex-col space-y-3">
            <div>
              <label class="mr-4" for="question">Question</label>
              <textarea
                id="creatingQuestion"
                rows="2"
                cols="30"
                v-model="creatingPoll.question"
              />
            </div>
            <div>
              <label class="mr-4" for="creatingDescription">Description</label>
              <textarea
                id="creatingDescription"
                rows="3"
                cols="45"
                v-model="creatingPoll.description"
              />
            </div>
            <div>
              <div
                class="my-4"
                v-for="(pa, k) in creatingPoll.answers"
                :key="'answer' + k"
              >
                <label class="mr-4" :for="'creatingAnswer' + k"
                  >Answer #{{ k + 1 }}</label
                >
                <input
                  type="text"
                  v-on:input="creatingPoll.answers[k] = $event.target.value"
                />
              </div>
              <button
                v-on:click="addAnswer"
                class="p-1 bg-yellow-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-700 focus:ring-opacity-75"
              >
                Add answer...
              </button>
            </div>
            <div>
              <label for="creatingMultiple"
                >Allow to select multiple answers</label
              >
              <input
                class="mx-3 mr-5"
                type="checkbox"
                id="creatingMultiple"
                v-model="creatingPoll.multiple"
              />
              <label for="creatingVisible"
                >Make results visible during poll</label
              >
              <input
                class="mx-3 mr-5"
                type="checkbox"
                id="creatingVisible"
                v-model="creatingPoll.visible"
              />
            </div>
            <div>
              <label for="creatingStartDate">Poll should start at:</label>
              <datepicker
                id="creatingStartDate"
                v-model="creatingPoll.startDate"
              />
              <label class="space-y-3" for="creatingEndDate"
                >(optional) Poll should end at:</label
              >
              <datepicker id="creatingEndDate" v-model="creatingPoll.endDate" />
            </div>
            <button
              v-on:click="createPoll"
              class="p-1 bg-yellow-500 text-white font-semibold rounded-lg shadow-md hover:bg-green-700 focus:ring-opacity-75"
            >
              Create a poll
            </button>
          </div>
        </tab>
      </tabs>
    </div>
  </div>
</template>

<script>
import Datepicker from "vue3-datepicker";
import { VueEcharts } from "vue3-echarts";
import FingerprintJS from "@fingerprintjs/fingerprintjs";
import Tabs from "./Tabs";
import Tab from "./Tab";
import { ref } from "vue";

let PollId = null;

const Replies = {
  taken: "Sorry but you appear to have taken the survey already.",
  noLocalStorage:
    "`localStorage API` not supported, this application will not work properly.",
  noCookieSet: "Sorry but you appear to have cleared your cookies",
  loaded: "App loaded successfully.",
  endpoint: "The endpoint ran into an error: ",
  tokenAcquired:
    "Token acquired! Check your email and then paste the result into the approprate field, under 'Your account')",
};

const Storage = {
  status: true,
  check() {
    if (!localStorage) this.status = false;
    return this.status;
  },
  set(user_storage) {
    localStorage.setItem("pollem-user", JSON.stringify(user_storage));
  },
  get() {
    return JSON.parse(localStorage.getItem("pollem-user"));
  },
  clear() {
    localStorage.removeItem("pollem-user");
  },
};

const Requests = {
  endpoints: ["http://localhost:8009", "https://pollem-now.herokuapp.com"],
  tryRoute(route) {
    const routes = [
      "/ask_token",
      "/confirm_token",
      "/create",
      "/close",
      "/get",
      "/take",
    ];
    if (routes.includes(route)) return route;
    else throw new Error("Wrong route: " + route);
  },
};

export default {
  name: "requests",
  components: {
    Tabs,
    Tab,
    VueEcharts,
    Datepicker,
  },
  data: () => {
    return {
      localStorage: true,
      creatingPoll: {
        startDate: null,
        endDate: null,
        question: "",
        description: "",
        multiple: false,
        visible: false,
        answers: ["Answer#1", "Answer#2"],
      },
      takingPoll: {},
      chart: {
        results: [],
        options: {},
      },
      user: {
        token: "",
        hash: "",
        email: "",
        fingerprint: "",
        created: [],
        taken: [],
        token_asked: false,
        token_sent: false,
      },
    };
  },
  setup() {
    // preparing tabs in relation to a possible GET request
    const uri = window.location.search.substring(1);
    PollId = new URLSearchParams(uri).get("poll_id");
    const active = PollId === null ? ref(0) : ref(1);
    return { active };
  },
  mounted() {
    // loading fingerprinting library
    FingerprintJS.load()
      .then((fp) => fp.get())
      .then((result) => {
        // loaded, binding fingerprint
        if (!Storage.check()) this.$toast.error(Replies.noLocalStorage);
        else {
          // restoring storage
          this.user = Storage.get() === null ? this.user : Storage.get();
          // saving fingerprint to component's data
          this.user.fingerprint = result.visitorId;
        }
        this.creatingPoll.startDate = new Date();
        // exiting loading if we're are not GET-ing any poll
        if (PollId === null) {
          this.$toast.success(Replies.loaded);
          return;
          // otherwise fetching poll passed as parameter
        }
        return (
          fetch(Requests.endpoints[1] + "/" + PollId.toString())
            // error, bubbling up to user
            .catch((err) => this.$toast.error(Replies.endpoints[1] + err))
            // parsing result
            .then((res) => res.json())
            // binding results to component's data, displaying, bubbling up confirmation
            .then((res) => {
              if (!res.get_poll) {
                this.$toast.error(
                  "Poll received but parsing failed. Unable to carry on."
                );
                return;
              }
              this.takingPoll = JSON.parse(res.get_poll);
              if (res.get_poll_results)
                this.chart.results = res.get_poll_results.map((d) =>
                  parseInt(d)
                );
              this.$toast.success(
                Replies.loaded +
                  " Here is your poll. (" +
                  res.get_poll_msg +
                  ")"
              );
            })
        );
      });
  },
  methods: {
    testChart() {
      const mockPoll = {
        startDate: new Date(),
        endDate: null,
        question: "How to make an omelette without breaking any egg?",
        description: "Some description",
        multiple: false,
        visible: true,
        answers: ["Answer#1", "Answer#2"],
      };
      const payload = {
        get_poll_results: [3, 5],
        get_poll: JSON.stringify(mockPoll),
        get_poll_msg: "Ok fine",
      };
      return Promise.resolve(JSON.stringify(payload))
        .then((res) => {
          const p = JSON.parse(res);
          this.takingPoll = JSON.parse(p.get_poll);
          if (p.get_poll_results) {
            this.chart.results = p.get_poll_results.map((d) => parseInt(d));
            this.setChartOptions();
          }
          this.$toast.success(p.get_poll_msg);
        })
        .catch((err) => this.$toast.error(err));
    },
    setChartOptions() {
      this.chart.options = {
        yAxis: {
          type: "category",
          data: this.takingPoll.answers,
        },
        xAxis: {
          type: "value",
        },
        series: [
          {
            data: this.chart.results,
            type: "bar",
            showBackground: false,
            backgroundStyle: {
              color: "rgba(180, 180, 180, 0.2)",
            },
          },
        ],
      };
    },
    addAnswer() {
      const i = this.creatingPoll.answers.length + 1;
      this.creatingPoll.answers.push("Answer#" + i.toString());
    },
    makeReq(route, payload) {
      const e = Requests.endpoints[1];
      const r = Requests.tryRoute(route);
      const url = e + r;
      if (route === "/get")
        return fetch(url)
          .catch((err) => this.$toast.error(err))
          .then((res) => res.json());
      else {
        const config = {
          method: "POST",
          headers: {
            "Accept": "application/json",
            "Content-type": "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Methods": "POST,GET,OPTIONS, PUT, DELETE",
            "Access-Control-Allow-Headers": "Accept, Content-Type, Content-Length, Accept-Encoding, X-CSRF-Token, Authorization"
          },
          body: JSON.stringify(payload),
        };
        return fetch(url, config)
          .catch((err) => this.$toast.error(err))
          .then((res) => res.json());
      }
    },
    askToken() {
      this.user.token_asked = true;
      const payload = {
        ask_email: this.user.email,
      };
      return this.makeReq("/ask_token", payload).then((res) => {
        this.$toast.success(Replies.tokenAcquired + " " + res.ask_token, {
          duration: 60000,
          dismissible: false,
        });
      });
    },
    confirmToken() {
      this.user.token_sent = true;
      const payload = {
        req_confirm_token: this.user.token,
        req_confirm_fingerprint: this.user.fingerprint,
        req_confirm_email: this.user.email,
      };
      return this.makeReq("/confirm_token", payload).then((res) => {
        if (res.resp_confirm_token && res.confirm_hash) {
          this.user.token = res.confirm_token;
          this.user.hash = res.confirm_hash;
          this.$toast.success(res.confirm_msg);
        } else {
          this.$toast.warning(res.confirm_msg);
        }
      });
    },
    createPoll() {
      const payload = {
        req_create_hash: this.user.hash,
        req_create_token: this.user.token,
        req_create_recipe: JSON.stringify(this.creatingPoll),
        req_create_startDate: this.creatingPoll.startDate,
      };
      if (this.creatingPoll.endDate !== null)
        payload.req_create_endDate = this.creatingPoll.endDate;
      return this.makeReq("/create", payload).then((res) =>
        this.$toast.success(res.create_msg)
      );
    },
    closePoll() {
      const payload = {
        close_hash: this.user.hash,
        close_token: this.user.token,
        close_pollid: PollId,
      };
      return this.makeReq("/close", payload).then((res) =>
        this.$toast.success(res.close_msg)
      );
    },
    takePoll() {
      const payload = {
        take_hash: this.user.hashs,
        take_token: this.user.token,
        take_fingerprint: this.user.fingerprint,
        take_pollid: PollId,
        take_answers: this.takingPoll.answers,
      };
      return this.makeReq("/take", payload).then((res) =>
        this.$toast.success(res.take_msg)
      );
    },
    logout() {
      this.user = {
        token: "",
        hash: "",
        email: "",
        fingerprint: "",
        created: [],
        taken: [],
        token_asked: false,
        token_sent: false,
      };
      Storage.clear();
    },
  },
  watch: {
    user(newUser) {
      if (Storage.status) Storage.set(newUser);
    },
  },
};
</script>
<style scoped>
body {
  padding-top: 40px;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>
